<template>
  <footer ref="footer">
    <video       
      autoplay loop playsinline muted>
      <source src="https://css-tricks-post-videos.s3.us-east-1.amazonaws.com/Island%20-%204141.mp4" type="video/mp4">
      Sorry, your browser doesn't support embedded videos.
    </video>
    <!--  -->
    <div class="container">
      <div class="flex content-between flex-wrap h-screen py-10">
        <div class="w-full">
          <ul class="mb-3">
            <li v-for="item in menu" :key="item.text">
              {{ item.text }}
            </li>
          </ul>
          <div class="flex">
            <a href="" target="_blank" class="icon-circle mr-4">
              <img src="@/assets/icon/fb.svg" alt="fb">
            </a>
            <a href="" target="_blank" class="icon-circle">
              <img src="@/assets/icon/in.svg" alt="fb">
            </a>
          </div>
        </div>
        <div class="footer">
          <div>
            <div class="mb-4">(65) 6534 4877</div>
            <p>6 Battery Road, Level 41, Singapore 049909</p>
          </div>
          <div>
            &copy; 2020 TSMP Law Corporation | <span class="link">Terms of Use</span>
          </div>
          <div>
            <img src="@/assets/img/logo.svg" alt="logo text" width="50" class="mx-auto mb-4">
            <img src="@/assets/img/logo_text.svg" alt="logo text" width="200">
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
import { useState } from "@/store";
export default {
  setup() {
    return { state: useState() }
  },
  data: () => ({
    menu: [
      { text: 'about us', route: '' },
      { text: 'our people', route: '' },
      { text: 'our expertise', route: '' },
      { text: 'news', route: '' },
      { text: 'careers', route: '' },
      { text: 'contact us', route: '' },
    ]
  }),
  methods: {
    handleScroll() {
      const footer = this.$refs.footer.offsetHeight
      const body = document.body.scrollHeight
      const scroll = body - footer - 82 // 82 tinggi navbar
      
      if (window.scrollY >= scroll) {
        this.state.hidden = true
      } else {
        this.state.hidden = false
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
footer{
  @apply w-screen h-screen text-white;
}
video{
  z-index: -1;
  @apply w-screen fixed left-0 bottom-0;
}
ul li{
  @apply text-2xl py-3 uppercase cursor-pointer;
}
ul li:hover{
  @apply opacity-75;
}
.icon-circle{
  width: 30px;
  height: 30px;
  @apply rounded-full border border-white flex items-center justify-center duration-100;
}
.icon-circle:hover{
  border-color: #B18A54;
  background-color: #B18A54;
}
.footer{
  @apply flex justify-between items-end font-sans w-full text-sm;
}
.link{
  @apply cursor-pointer duration-100;
}
.link:hover{
  color: #B18A54;
}
</style>