<template>
  <header 
    :class="{
      'bg-primary': !transparent,
      'hidden': state.hidden,
    }">
    <div class="container">
      <div class="navbar">
        <div>
          <img src="@/assets/img/logo.svg" alt="logo" width="50">
        </div>
        <nav>
          <div v-for="item in nav" :key="item.text">
            {{ item.text }}
          </div>
        </nav>
        <div class="flex items-center relative">
          <img src="@/assets/icon/search.svg" alt="icon" width="30" class="mr-8">
          <div 
            class="burger-menu" 
            :class="{'open': state.active}"
            @click="state.active=true">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import { useState } from "@/store";
export default {
  setup() {
    return { state: useState() }
  },
  data: () => ({
    nav: [
      { text: 'our expertise', route: '' },
      { text: 'forefront', route: '' },
      { text: 'community', route: '' }
    ],
    transparent: true
  }),
  methods: {
    handleScroll() {
      if (window.scrollY > 400) {
        this.transparent = false
      } else {
        this.transparent = true        
      }
    }
  },  
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
header{
  @apply fixed w-full z-10 duration-100;
}
.navbar{
  @apply flex justify-between items-center py-4 text-white font-sans;
}
nav{
  @apply flex;
}
nav div{
  @apply duration-200 cursor-pointer uppercase px-3;
}
nav div:hover{
  @apply opacity-75;
}
.burger-menu{
  width: 40px;
  height: 25px;
  @apply flex content-between flex-wrap cursor-pointer overflow-hidden;
}
.burger-menu span{
  width: 100%;
  height: 2px;
  @apply bg-white;
}
.burger-menu.open span:nth-child(1) {
  transform: translateX(100%);
  transition: .25s ease-in-out;
}

.burger-menu.open span:nth-child(2) {
  transform: translateX(100%);
  transition: .5s ease-in-out;
}

.burger-menu.open span:nth-child(3) {
  transform: translateX(100%);
  transition: .75s ease-in-out;
}
</style>
